<template>
<div class="waiting-confirmation">
  <van-dialog v-model:show="showDialog" title="" :showConfirmButton="false">
    <div class="confirmation-box">
      <div class="confirmation-top flex-between">
        <div class="title" v-if="isWaiting">{{$t('index.Waiting for confirmation')}}</div>
        <div class="title" v-else>{{$t('index.Transaction submitted')}}</div>
       <svg  @click="showDialog = false" viewBox="0 0 24 24" class="primary" color="primary" width="20px" xmlns="http://www.w3.org/2000/svg"><path d="M18.3 5.70997C17.91 5.31997 17.28 5.31997 16.89 5.70997L12 10.59L7.10997 5.69997C6.71997 5.30997 6.08997 5.30997 5.69997 5.69997C5.30997 6.08997 5.30997 6.71997 5.69997 7.10997L10.59 12L5.69997 16.89C5.30997 17.28 5.30997 17.91 5.69997 18.3C6.08997 18.69 6.71997 18.69 7.10997 18.3L12 13.41L16.89 18.3C17.28 18.69 17.91 18.69 18.3 18.3C18.69 17.91 18.69 17.28 18.3 16.89L13.41 12L18.3 7.10997C18.68 6.72997 18.68 6.08997 18.3 5.70997Z"></path></svg>
    
      </div>
    <div class="icon-box">
       <div class="svg-box">
        <van-loading v-if="isWaiting" size="90" color="#1fc7d4"/>
        <svg v-else xmlns="http://www.w3.org/2000/svg" class="loading" width="97" height="97" viewBox="0 0 24 24" fill="none" stroke="#1FC7D4" stroke-width="0.5" stroke-linecap="round" stroke-linejoin="round"><circle cx="12" cy="12" r="10"></circle><polyline points="16 12 12 8 8 12"></polyline><line x1="12" y1="16" x2="12" y2="8"></line></svg>
        </div>
    </div>
    <div class="info-text" v-if="isWaiting">{{$t('index.Confirm this transaction in your wallet')}}</div>
     <div class="primary-btn-box" v-else>
            <van-button type="primary" block @click="showDialog = false">{{$t('index.Close')}}</van-button>
          </div>
    </div>
    
 </van-dialog>
</div>
</template>

<script>
import { reactive, toRefs, computed } from "vue";

export default {
  components: {
  },
  props: {
    show: {
      type: Boolean,
      default: false
    },
    isWaiting: {
      type: Boolean,
      default: true
    }
  },
  setup (props, {emit}) {
  
    const data = reactive({
      showDialog: computed({
        get () {
          return props.show
        },
        set (value) {
          emit('updateShow', value)
        }
      })
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.primary-btn-box{
 padding: 0 20px;
}
.primary{
  fill: rgb(31, 199, 212);
}
.confirmation-box{
  padding: 20px;
  .title{
     color: var(--primary-color);
     font-size: 16px;
     font-weight: 600;
  }
  .svg-box{
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin: 20px 0;
    .loading{
      width: 90px;
      height: 90px;
      display: block;
    //   animation: rotation 3s linear infinite;
      animation:2s linear 0s infinite normal none running rotation;
    }
  }
  .info-text{
    color: var(--primary-color);
    font-size: 16px;
  }
}

</style>

<style scoped>
.loading{
      width: 90px;
      height: 90px;
      display: block;
      animation:2s linear 0s infinite normal none running rotation;
 }
@keyframes rotation {
  0% {
    transform: rotate(0deg);
}
100% {
    transform: rotate(360deg);
}
}
</style>
